.video-grid__wrapper{width:100%;margin-top:20px;display:flex;flex-flow:row wrap;justify-content:flex-start;align-items:flex-start}.video-grid__card{width:calc((100%/3) - 11px - 0.01px);margin-bottom:20px;display:flex;flex-flow:column;justify-content:center;align-items:center}.video-grid__card:nth-child(3n+2){margin-right:16px;margin-left:16px}.video-grid__card__video-wrapper{position:relative;padding-bottom:56.25%;width:100%;height:auto;content:"";display:block}.video-grid__card__video-wrapper iframe{position:absolute;top:0;left:0;width:100%;height:100%;border-width:0;outline-width:0}.video-grid__card__title-wrapper{width:100%;margin-top:10px;text-align:left;line-height:18px}.video-grid__card__title{font-size:14px;font-weight:600}.video-grid__card__subtitle{font-size:14px;color:rgba(23,23,25,0.3)}

.video-list__wrapper{width:100%;margin-top:20px;display:flex;flex-flow:column;justify-content:flex-start;align-items:flex-start}.video-list__card{width:100%;margin-bottom:20px;display:flex;flex-flow:row nowrap;justify-content:flex-start;align-items:stretch}.video-list__card__video-wrapper{position:relative;display:block;width:26%;height:auto;padding-bottom:12%}.video-list__card__video-wrapper iframe{position:absolute;top:0;left:0;width:100%;height:100%;border-width:0;outline-width:0}.video-list__card__title-wrapper{width:35%;margin-left:20px;padding:10px 0;display:flex;flex-flow:column;justify-content:space-between;text-align:left;line-height:18px}.video-list__card__title{font-size:16px;font-weight:600}.video-list__card__subtitle{font-size:14px;color:rgba(23,23,25,0.3)}

.page-search__form-container{width:100%}.page-search__form__slider-container{display:flex;justify-content:space-between}.page-search__form__slider-container>div:nth-child(1){width:75%}.page-search__form__slider{flex-grow:1}

.page-search__container{margin-top:12vw;display:flex;flex-flow:column;justify-content:center;align-items:center;transition:margin .2s ease-in-out}.page-search__container.--shrinked{margin-top:2vw}.page-search__pannel{width:100%;margin-top:40px;display:flex;flex-flow:row;justify-content:space-between;align-items:center;font-size:16px;line-height:24px}.page-search__pannel__title{display:flex;flex-flow:row nowrap;justify-content:flex-start;align-items:center}.page-search__pannel__title span{font-weight:600}.page-search__pannel__title__total-results{margin-left:25px;color:rgba(23,23,25,0.3)}.page-search__pannel__btns svg{cursor:pointer;opacity:0.3;transition:opacity .15s ease-in}.page-search__pannel__btns svg.--active{opacity:1}.page-search__pannel__btns svg:nth-child(1){margin-right:15px}.page-search__pannel__btn-modal{font-size:20px;opacity:0.5}.page-search__pannel__btn-modal.--active:hover{opacity:1;color:#1890ff}

.page-favorites__container{margin-top:2vw;display:flex;flex-flow:column;justify-content:flex-start;align-items:flex-start}.page-favorites__list{width:100%;margin-top:2rem !important;font-size:1.2rem !important;background-color:#fff}.page-favorites__list .ant-empty-normal{height:40vh;display:flex;justify-content:center;align-items:center}.page-favorites__list__btns-container{flex-basis:35%;display:flex;flex-flow:row nowrap;justify-content:space-between;align-items:center}.page-favorites__list__btns-container__btn-grp>:nth-child(1){margin-right:25px}

.page-auth__wrapper{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}.page-auth__form-container{width:450px;height:500px;display:flex;flex-flow:column;justify-content:center;align-items:center;background-color:#FFFFFF;border-radius:5px;border:1px solid rgba(39,39,39,0.1)}.page-auth__form__title{margin-bottom:20px;font-size:18px;font-weight:700}.page-auth__form__logo{margin-bottom:32px}.page-auth__form{width:60%}.page-auth__form label{color:rgba(23,23,25,0.3) !important}.page-auth__form__space{width:100%}.page-auth__form__space>div{width:100%}.page-auth__error{padding-top:36% !important}


body{margin:0;background-color:#FAFAFA;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

